/* Start Portfolio Section*/
.portfolio{
	padding: $sec-padding;
	background-color: $second-color;

	


	.control-box{

		margin-bottom: 30px;

		button{
			background: transparent;
			border: 0;
			color: $white-color;
			text-transform: capitalize;
			padding: 10px;
			outline: none;
			font-weight: 400;

			@media(max-width:768px){

				font-size: 14px;
				padding: 10px 5px;


			}

			&.active{
				text-decoration: line-through;
				color: $theme-color;
			}
		}
	}

	.port-box{
		margin-bottom: 30px;
		position: relative;
		@include Transition(all,1s,ease); 
		overflow: hidden;

		.port-overlay{

			@include overlay;
			background-color: rgba($second-color,.7);
			@include Transition(all,1s,ease); 
			opacity: 0;


			a{

				text-decoration: none;
				position: absolute;
				top: 40%;
				left: 50%;
				transform: translate(-50%);

				transfomr:translateY();
				i{
					color:$first-color;
					background-color: rgba($white-color,.3);
					border-radius: 50%;
					font-size: 18px;
					width: 50px;
					height: 50px;
					line-height: 50px;
					text-align: center;
					display: block;
					@include Transition(all,1s,ease); 

					&:hover{

						background-color: rgba($white-color,.8);
						color: $second-color;
					}
				}

			}
		}

		img{
		width: 100%;
		@include Transition(all,1s,ease);
	}

		&:hover{

			.port-overlay{

				opacity: 1;

			}
			img{

				transform: scale(1.1);
			}
		}




	}

	

}

/* End Portfolio Section*/